@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
@media only screen and (max-width: 650px){.app{flex-direction:column !important}}.app{display:flex;flex-wrap:nowrap;flex-direction:row}

@media only screen and (max-width: 650px){.sidebar{width:100% !important;min-height:0 !important}.sidebar__links{width:100% !important;position:static !important}.sidebar__link{font-size:1.4em !important}.sidebar__section-title{font-size:1.5em !important}}.sidebar{width:300px;background-color:#318CE7;min-height:100vh;z-index:10}.sidebar__links{padding:30px 10px 30px 30px;display:flex;flex-direction:column;position:fixed;left:0;width:300px}.sidebar__link{color:oldlace;font-size:1.5em;padding:6px}.sidebar__link_active{background-color:#1880E7;border-radius:5px}.sidebar__icon{height:1.2em;margin-right:7px}.sidebar__section-title{font-size:1.7em;color:oldlace;font-weight:600;margin-top:15px;margin-bottom:10px}

.create-project-btn{padding:6px;background-color:#fff;color:#1880E7;margin-top:10px;cursor:pointer;border:0;border-radius:5px}.create-project-btn:focus{outline:none}.create-project-btn__input,.create-project-btn__btn{border:0;border-radius:3px;padding:4px;margin-top:2px}.create-project-btn__error{text-align:center;color:#CC397B}.create-project-btn__btn{color:#1880E7;background-color:#fff}

@media only screen and (max-width: 650px){.list__header{position:static !important}.list__container{padding-left:10px !important;padding-right:10px !important}}.list{flex-grow:1}.list__container{padding-left:40px;padding-right:40px}.list__header{position:fixed;background-color:#fff;box-shadow:0 3px 10px 0.5px #eee;padding-bottom:20px;padding-top:20px;width:100%}.list__title{font-size:24px;font-weight:600}.list__todos{padding-top:90px}.list__icon{height:1.5em}.list__delete-btn{border:0;background:0;cursor:pointer;margin-left:1em}.list__delete-btn:focus{outline:0}.list__add-btn{cursor:pointer;border:2px solid #318CE7;border-radius:13px;background-color:#fff;color:#318CE7;padding:7px;font-size:1.2em;display:flex;align-content:center;margin-bottom:20px}.list__add-btn:focus{outline:0}.list__add-btn img{height:1.6em;margin-left:0.3em}

.todo{display:flex;align-items:center;margin-bottom:5px;background-color:#71B8FF;padding:3px;border-radius:3px}.todo__checkbox{height:1.3em;width:1.8em}.todo__input{border:0;flex-basis:3;background-color:#71B8FF;resize:none;flex-grow:1;color:#fff}.todo__input:focus{outline:0}.todo__input_checked{text-decoration:line-through}.todo__icon{height:1em;width:1em}.todo__delete-btn{cursor:pointer;background-color:#71B8FF;border:0}.todo__delete-btn:focus{outline:0}

*{margin:0;padding:0;font-family:"Open Sans",sans-serif;font-size:16px;box-sizing:border-box}a{text-decoration:none;color:#0247FE}

